import React, { useState } from "react";
import EditorBox from "./EditorBox";
import "./ChatBox.css";

function ChatBox(props) {
  let classNameSet = null;
  let opinionDiv = null;
  if (props.opinion == "") {
    opinionDiv = <div className="pro"></div>;
    classNameSet = "pro";
  } else if (props.opinion == "") {
    opinionDiv = <div className="con"></div>;
    classNameSet = "con";
  } else if (props.opinion == "") {
    classNameSet = "free";
  }
  const [visible, setVisible] = useState(false);

  return (
    <li className="chatBox">
      <div className="ChatBox-container">
        <div className={"userImg" + classNameSet}>
          <img
            src="https://cdn-icons-png.flaticon.com/512/4553/4553212.png"
            height="40"
            width="40"
          ></img>
          {opinionDiv}
        </div>
        <div className={"msg-container " + "msg" + classNameSet}>
          <h5>{props.user}</h5>
          <p dangerouslySetInnerHTML={{ __html: props.content }}></p>
        </div>
        <div className={"reaction " + "reaction" + classNameSet}>
          {/* <span></span> */}
          <button
            onClick={() => {
              setVisible(!visible);
            }}
          >
            
          </button>
        </div>
        <div className={visible ? "reply" : ""}>
          {visible && <EditorBox value="" />}
        </div>
      </div>
    </li>
  );
}

export default ChatBox;
