import './Join.css';
import {useState} from 'react';
import {Routes, Route, Link, useNavigate} from 'react-router-dom'
import axios from "axios";

function Join() {
    let navigate = useNavigate()

    const [id, setId] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [email, setEmail] = useState("");

    const onIdHandler = (event) => {
        setId(event.currentTarget.value);
    };

    const onPasswordHandler = (event) => {
        setPassword(event.currentTarget.value);
    };

    const onConfirmPasswordHandler = (event) => {
        setConfirmPassword(event.currentTarget.value);
    };

    const onNameHandler = (event) => {
        setEmail(event.currentTarget.value);
    };


    const handleSubmit = async (event) => {
        event.preventDefault();
        try {
            await axios({
                    method: 'post',
                    url: 'http://localhost:8080/user/register',
                    data: {
                        "userName": id,
                        "password": password,
                        "email":email
                    }
                }
            ).then((data) => {
                if(data.status===200 && data.data != "sameIdExist"){ // 
                    console.log(data)
                    console.log("!!")
                    window.location.replace('http://localhost:3000/login')
                }else if (data.data == "sameIdExist") {
                    alert(" ID .  !!")
                } else {
                    console.log("  !!");
                }
            });
        } catch (error) {
            console.log(error)

        }

    };


    return (
        <div className="Join">
            <div className="Join_Box">
                <div>
                    <p id="id_text"></p>
                    <input type="text" id="id_input"
                           value={id} onChange={onIdHandler}
                           placeholder=" "/>
                </div>
                <div>
                    <p id="pass_text"></p>
                    <input type="password" id="password_input2"
                           value={password} onChange={onPasswordHandler}
                           placeholder=" "></input>
                </div>
                <div>
                    <p id="pass_text"> </p>
                    <input type="password" id="password_input3"
                           value={confirmPassword} onChange={onConfirmPasswordHandler}
                           placeholder="  "></input>
                    {password !== confirmPassword ?
                        <p id="notice">*   .</p>
                        : null
                    }
                </div>
                <div>
                    <p id="pass_text"></p>
                    <input type="email" id="name_input"
                           value={email} onChange={onNameHandler}
                           placeholder=" "/>
                </div>
                <div>
                    <button className="jo_button2" onClick={handleSubmit}></button>
                </div>
            </div>
        </div>
    );
}

export default Join;
  