import React from "react";
import "./Modal.css";

function Date() {
  return (
    <div>
      <p></p>
      <input type="date" name="userSetDate" />
    </div>
  );
}

const Modal = (props) => {
  const { open, close, type } = props;

  return (
    //  openModal
    <div className={open ? "openModal modal" : "modal"}>
      {open ? (
        <section>
          <header>
            <button className="close" onClick={close}>
              &times;
            </button>
          </header>
          <main>
            <form>
              <div>
                <input type="text" name="topic" placeholder="" />
              </div>
              <div>
                <textarea
                  cols="55"
                  rows="10"
                  name="userExplan"
                  placeholder=""
                />
              </div>
              {type == "debate" && <Date />}
            </form>
          </main>
          <footer>
            <button></button>
          </footer>
        </section>
      ) : null}
    </div>
  );
};

export default Modal;
